import React from 'react';
import './App.css';
import Nav from './Nav';
import Plateau from './Plateau';
import Play from './Play';
import Modal from './Modal';


class App extends React.Component {
  constructor(props) {
    super(props)  
    this.state = {
      iswin : "loose",
      currentCase: 0,
      currentLine: 0,
      message: "",
      combinationToFind: this.generateCombination(),
      game: [
        [[], []],
        [[], []],
        [[], []],
        [[], []],
        [[], []],
        [[], []],
        [[], []],
        [[], []],
        [[], []],
        [[], []],
      ]
    } 
    this.change = this.change.bind(this)
    this.undo = this.undo.bind(this)
    this.compare = this.compare.bind(this)
    this.submit = this.submit.bind(this)
    this.closeModal = this.closeModal.bind(this)
    this.generateCombination = this.generateCombination.bind(this)
  }

  change(color) {
    const gameBis = [...this.state.game]
    if (this.state.currentCase < 4) {
      gameBis[this.state.currentLine][0].push(color)
    this.setState(state => ({
      game: gameBis,
      currentCase: state.currentCase + 1,
    })) 
    }
  }

  undo() {
    const gameBis = [...this.state.game]
    if (this.state.currentCase > 0) {
      gameBis[this.state.currentLine][0].pop()

    this.setState(state => ({
      game: gameBis,
      currentCase: state.currentCase - 1,
    })) 

    }
      }

    compare() {
      let combination = [...this.state.game[this.state.currentLine][0]]
      let combinationToFind = [...this.state.combinationToFind]
      let good = 0
      let bad = 0
      for (let index = 0; index < combination.length; index++) {
          if (combination[index] == combinationToFind[index]) {
            good ++
            combinationToFind[index] = "z"
            combination[index] = "a"
          }
      }
      for (let index = 0; index < combinationToFind.length; index++) {
            if (combinationToFind.includes(combination[index])) {
              console.log("bad")

              bad ++
              combinationToFind[combinationToFind.indexOf(combination[index])] = "a"
              combination[index] = "z"
            }     
      }
      return [good,bad]
    }
    submit () {
      let combinationToFind = [...this.state.combinationToFind]
      let currentLine = this.state.currentLine
      let currentCase = this.state.currentCase
      let gameBis = [...this.state.game]
      if (this.state.currentCase == 4) {
        let result = this.compare()
        console.log(result);
        
        for (let l = 0; l < result[0]; l++) {
          gameBis[this.state.currentLine][1].push("black")
        }
        for (let l = 0; l < result[1]; l++) {
          gameBis[this.state.currentLine][1].push("gray")
        }
        if (result[0] == 4) {
          this.setState(state => ({
            iswin : "win",
            message: "tu as win gg"
          })) 
          } 
        if (currentLine == 9 &&  result[0] != 4) {
          this.setState(state => ({
            iswin : "win",
            message: "tu as loose desolé gros la combinaison était"+" "+ combinationToFind
          })) 
        }

        this.setState(state => ({
          game: gameBis,
          currentLine: state.currentLine + 1,
          currentCase: 0
        }))
      }
    }

    closeModal() {
      this.setState(state => ({
        iswin : "loose",
        currentCase: 0,
        currentLine: 0,
        game: [
          [[], []],
          [[], []],
          [[], []],
          [[], []],
          [[], []],
          [[], []],
          [[], []],
          [[], []],
          [[], []],
          [[], []],
        ]
      })) 
    }

    generateCombination() {
        let combinate = []
        for (let i = 0; i < 4; i++) {
            let random = Math.floor(Math.random()*6)
            let colors = ["red", "blue", "violet", "yellow", "green", "grey"]
            combinate.push(colors[random])
        }
        return combinate
    }  
  render() {
    return (
    <div className="App">
      <Nav></Nav>
        <Plateau currentLine={this.state.currentLine} game={this.state.game}>
        </Plateau>
        <Play colorize={this.change} undo={this.undo} submit={this.submit}></Play>
        <Modal message={this.state.message} closeModal={this.closeModal} isWin={this.state.iswin}></Modal>
        
    </div>
    );
  }
}

export default App;
