import React from 'react';
import Rond from './Rond';

function Plateau(props) {
    const play = props.game.map((element, index) => (
        <Rond key={index} selected={props.currentLine == index} line={element}></Rond>
    ))
    return (
        <div className="plateau">
            {play}
        </div>
    )
}
export default Plateau;
